<template>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        <div class="container">
            <router-link to="/"
                class="navbar-brand mr-auto">
                Employee Manager
            </router-link>  
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse " id="navbarNavAltMarkup">
                <div class="navbar-nav ml-auto">
                    <a v-if="isLoggedIn" class="nav-item nav-link mr-3">{{ currentUser.email }}</a>
                    <router-link v-if="isLoggedIn" to="/" class="nav-item nav-link" exact active-class="active">Dashboard</router-link>
                    <router-link v-if="isLoggedIn" to="/New" tag="button" class="ml-2 btn btn-success btn-sm">Add New Employee <i class="fa fa-plus"></i></router-link>
                    <button v-if="isLoggedIn" class="ml-2 btn btn-light btn-sm" @click="logout">Logout </button>
                    <router-link v-if="!isLoggedIn" to="/Login" class="nav-item nav-link" exact active-class="active">Login</router-link>
                    <router-link v-if="!isLoggedIn" to="/Register" class="nav-item nav-link" exact active-class="active">Register</router-link>
                </div>
            </div>
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase'
export default {
    data(){
        return{
            isLoggedIn: false,
            currentUser: null,
        }
    },
    
    created(){
        this.fetchData()
    },
    watch:{
        '$route': 'fetchData'
    },
    methods:{
        logout(){
            firebase.auth().signOut().then(() => {
                this.$router.push('/Login');
            })
        },
        fetchData(){
            if(firebase.auth().currentUser){
                this.isLoggedIn = true
                this.currentUser = firebase.auth().currentUser
            } else {
                this.isLoggedIn = false
                this.currentUser = ''
            }
        }
    }
}
</script>
